.hero-section {
  // .hero-background {
  //   position: absolute;
  //   top: 0;
  //   right: 0;
  //   left: 0;
  //   width: 100%;
  //   z-index: -1;
  // }
  position: relative;
  background-image: url(../images/hero-bg/hero-bg-mobile@1x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position-y: -290px;
  max-width: 100%;
  width: 100%;

  .img {
    margin-left: auto;
    margin-right: auto;
  }
  .button {
    display: block;

    &:focus,
    &:hover {
      fill: $accentColor;
      background-color: $basicWhiteColor;
    }
  }
  .hero-content:last-child {
    margin-top: 72px;
    @media screen and (min-width: 768px) {
      margin-top: 142px;
    }
    @media screen and (min-width: 1344px) {
      width: 100%;
    }
  }
  .hero-content:not(:last-child) {
    @media screen and (max-width: 767px) {
      padding-bottom: 36px;

      .button {
        margin-right: auto;
        margin-left: auto;
      }
    }
  }
  @media screen and (min-width: 768px) {
    background-image: url(../images/hero-bg/hero-bg-tablet@1x.png);
    background-position-y: -170px;
  }
  @media screen and (min-width: 1344px) {
    background-image: url(../images/hero-bg/hero-bg-desktop@1x.png);
    background-position-y: -200px;
  }
}

.hero-container {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;

    .hero-content {
      width: 50%;
    }
  }
}

.hero-title {
  @include font(800, 20px, 1.35, 0.03em);
  color: $primaryTitleColor;
  margin-bottom: 30px;

  .hero-label {
    display: block;
  }
  @media screen and (min-width: 768px) {
    font-size: 26px;
  }
  @media screen and (min-width: 1344px) {
    font-size: 58px;
  }
}

.hero-description {
  margin-bottom: 30px;
  @include font(700, 14px, 1.57, 0.08em);

  .grey-color {
    display: block;
    font-weight: 500;
    color: $primaryTextColor;
  }

  .orange-color {
    color: $accentColor;
  }
  @media screen and (min-width: 1344px) {
    font-size: 20px;
  }
}

.hero-teacher {
  display: flex;
  justify-content: center;
}
